body,
html {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

img {
  max-width: 100%;
  height: auto;
}

.stats {
  width: 50%;
  margin: 0 auto;
  margin-bottom: 113px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}
@media only screen and (min-width: 768px) {
  .stats {
    width: 90%;
    flex-wrap: nowrap;
  }
}
.stats .unique {
  width: 50%;
}
@media only screen and (min-width: 768px) {
  .stats .unique {
    display: flex;
    flex-direction: column;
    width: 90%;
  }
}
.stats h3 {
  font-weight: 600;
  font-size: 48px;
  line-height: 56px;
  letter-spacing: 0.5px;
  text-align: center;
  margin-bottom: 10px;
}
.stats p {
  text-transform: uppercase;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.5px;
  color: rgba(18, 18, 18, 0.6980392157);
  text-align: center;
  margin: 0;
}

.stats2 {
  width: 90%;
  margin: 0 auto;
  margin-bottom: 7rem;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: space-evenly;
}
.stats2 .row .services {
  text-align: center;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 16px;
  line-height: 26px;
  letter-spacing: 1.5px;
  color: #5e3bee;
}
.stats2 .row h3 {
  font-weight: 600;
  font-size: 44px;
  line-height: 56px;
  letter-spacing: 0.5%;
  text-align: center;
  margin: 13px 0px;
}
.stats2 .row p {
  font-weight: 400;
  font-size: 16px;
  line-height: 32px;
  letter-spacing: 0.5px;
  text-align: center;
  width: 50%;
  color: rgba(18, 18, 18, 0.6980392157);
  margin: 0 auto;
}
.stats2 .row2 {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin-top: 64px;
}
@media screen and (min-width: 1024px) {
  .stats2 .row2 {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    gap: 40px;
  }
}
@media screen and (min-width: 1440px) {
  .stats2 .row2 {
    flex-wrap: nowrap;
  }
}
.stats2 .row2 .box {
  text-align: center;
  width: 90%;
  box-shadow: 37px 37px 80px 0px rgba(0, 0, 0, 0.1019607843);
  border-radius: 24px;
}
@media screen and (min-width: 1024px) {
  .stats2 .row2 .box {
    width: 35%;
  }
}
@media screen and (min-width: 1440px) {
  .stats2 .row2 .box {
    width: 90%;
  }
}
.stats2 .row2 .box .service-img {
  padding: 3rem 0 0 0;
}
.stats2 .row2 .box h2 {
  font-weight: 500;
  font-size: 20px;
  line-height: 32px;
  letter-spacing: 0.5px;
}
.stats2 .row2 .box p {
  font-weight: 400;
  font-size: 16px;
  line-height: 32px;
  letter-spacing: 0.5;
  color: rgba(18, 18, 18, 0.6980392157);
  width: 50%;
  margin: 0 auto;
  padding-bottom: 52px;
}